import { violetLight } from 'otkit-colors/token.common';
import { registerTheme } from '../registerTheme';
import defaultTheme from '../../../theme/defaultTheme';


describe('registerTheme', () => {
  describe('Default theme', () => {
    const expectedResult = defaultTheme;
    it('Should return theme object', () => {
      expect(registerTheme()).toEqual(expectedResult);
    });
  });

  describe('Custom theme', () => {
    it('Should return overwritten theme', () => {
      const customTheme = {
        common: {
          fill: 'red',
        },
        label: {
          fontSize: '20px',
        },
      };
      const controlTheme = registerTheme();
      expect(controlTheme.common.fill).toEqual(violetLight);
      expect(controlTheme.label.fontSize).toEqual('13px');

      const overwriteTheme = registerTheme(customTheme);
      expect(overwriteTheme.common.fill).toEqual('red');
      expect(overwriteTheme.label.fontSize).toEqual('20px');
    });
  });
});
